.gameboard__result { position: relative; z-index: 2; display: flex; flex-direction: column; justify-content: center; align-items: center; width: 100%; margin: 0 auto; transform: scaleX(1); transform-origin: center; transition: transform 0.5s ease-in, width 0.5s ease-in; }

@media (min-width: 375px) { .gameboard__result { margin-top: 46px; } }

@media (min-width: 1024px) { .gameboard__result { width: max-content; margin: 0; margin-top: 58px; margin-right: 70px; } }

.gameboard__result--hidden { visibility: hidden; transform: scaleX(0); }

@media (min-width: 1024px) { .gameboard__result--hidden { visibility: unset; width: 0; } }

.gameboard--extended .gameboard__stage-1 > svg { padding-top: 6px; }

.gameboard--extended .gameboard__stage-1 .gameboard__element { width: 97px; height: 97px; }

.gameboard--extended .gameboard__stage-1 .gameboard__element::before { top: calc(50% - 36px); left: calc(50% - 36px); width: 72px; height: 72px; }

.gameboard--extended .gameboard__stage-1 .gameboard__element svg { transform: scale(0.7); }

.gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(1) { top: 2%; left: 35%; }

.gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(2) { top: 29%; left: 3%; }

.gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(3) { top: 29%; left: 68%; }

.gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(4) { top: 69%; left: 16%; }

.gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(5) { top: 69%; left: 55%; }

@media (min-width: 1024px) { .gameboard--extended .gameboard__stage-1 .gameboard__element { width: 145px; height: 145px; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(1) { top: -16px; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(2) { top: 27%; left: 0; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(3) { top: 27%; left: 69%; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(4) { top: 72%; left: 14%; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element:nth-of-type(5) { top: 72%; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element::before { top: calc(50% - 55px); left: calc(50% - 55px); width: 110px; height: 110px; }
  .gameboard--extended .gameboard__stage-1 .gameboard__element svg { transform: scale(1); } }

@media (min-width: 1024px) { .gameboard--extended .gameboard__stage-1 > svg { padding-top: 24px; } }

.btn { padding: 10px 36px; text-transform: uppercase; letter-spacing: 2px; color: #f6f6f6; background-color: transparent; border: 2px solid #606e85; border-radius: 10px; box-shadow: none; transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out; cursor: pointer; }

@media (hover: hover), screen and (min-width: 0\0) { .btn:hover, .btn:focus { color: #3b4363; background-color: #f6f6f6; border-color: #f6f6f6; } }

.btn--white { color: #3b4363; background-color: #f6f6f6; border-color: #f6f6f6; }

@media (hover: hover), screen and (min-width: 0\0) { .btn--white:hover, .btn--white:focus { color: #dc2e4e; } }

.btn--lg { padding: 12px 60px; }

.link { border-bottom: 1px solid transparent; }

@media (hover: hover), screen and (min-width: 0\0) { .link:hover, .link:focus { border-color: inherit; } }

